*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 16px;
}

body {
  font-family: "SuisseIntl", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  line-height: 1;
  font-weight: 400;
  background-color: #ffffff;
  color: #20262f;
}

a {
  text-decoration: none;
}

img {
  height: auto;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
p,
figure,
fieldset,
ul {
  margin: 0;
}

